/* Стили для таблицы */
.table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 24px;
}

.table th,
.table td {
    padding: 12px;
    border: 1px solid #ddd;
    text-align: left;
}

.table th {
    background-color: #007bff;
    color: #ffffff;
}

.table tr:nth-child(even) {
    background-color: #f9f9f9;
}